@import url(https://fonts.googleapis.com/css2?family=Jost:wght@500&display=swap);
body {
  background-color: #fbd7d5;
  font-family: 'Jost', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  margin-left: auto;
  margin-right: auto;
  max-width: 800px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* preventing initial flash? */
/* html.wf-loading * {
  opacity: 0;
} */

/* App styles  */
.App {
  text-align: center;
  margin-top: 100px;
}
.Header {
  background-color: white;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  font-size: 40px;
  margin-top: -100px;
  margin-bottom: 10px;
  width: 100%;
  max-width: 800px;
  padding: 10px;
  position: fixed;
  z-index: 1;
}

/* common styles for the different pages */
.divider {
  border: .75px solid #a79797;
  margin-top: 10px;
  margin-bottom: 20px;
}
.link {
  color: blue !important;
}
.link:hover {
  text-decoration: underline;
}
.pageTitle {
  color: #0008e5;
}


/* about me page */
.picture {
  width: 100%;
  margin-bottom: 10px;
}
.col-pic {
  min-width: 250px;
  max-width: 75vw;
  margin-left: auto;
  margin-right: auto;
}
.col-bio {
  text-align: left;
  margin-top: auto;
  margin-bottom: auto;
  min-width: 250px;
}

/* projects and other page */
.col-vid {
  margin: auto;
}
.col-description {
  text-align: left;
  margin-top: auto;
  margin-bottom: auto;
  min-width: 250px;
}

/* home page */
a:link, a:visited, a:active, a:focus {
  color: black;
  text-decoration: none;
}

a:hover {
  color: #0008e5;
}
.Home {
  width: 100%;
}
.navItems {
  font-size: 38px;
  text-align: center;
}
.navRow {
  display: flex;
  padding-bottom: 15px;
  justify-content: center;
}
.navNameCol {
  width: 130px;
  display: flex;
  flex-direction: row-reverse;
}
.navIcon {
  height: 45px;
  max-width: 50px;
  margin-left: 15px;
}

